.widget.widget-settings .toggle-item {
    border-bottom: 1px solid $navBackGround;
    display: inline-block;
    width: 100%;
    padding: 10px 0px;
}

.widget.widget-settings .toggle-item em {
    color: $btnPrimaryColor;
}


.toggles input[type='checkbox'].toggle {
    display: inline-block;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 55px;
    height: 28px;
    background-color: $whiteColor;
    position: relative;
    -webkit-border-radius: 30px;
    -moz-border-radius: 30px;
    -ms-border-radius: 30px;
    border-radius: 30px;
    -moz-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}

.toggles input[type='checkbox'].toggle:after {
    content: '';
    display: inline-block;
    position: absolute;
    width: 24px;
    height: 24px;
    background-color: $btnPrimaryColor;
    top: 2px;
    left: 2px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    border-radius: 50px;
    -moz-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}
.toggles input[type='checkbox']:checked.toggle {
    -moz-box-shadow: inset 0 0 0 15px $btnPrimaryColor;
    -webkit-box-shadow: inset 0 0 0 15px $btnPrimaryColor;
    box-shadow: inset 0 0 0 15px $btnPrimaryColor;
}
.toggles input[type='checkbox']:checked.toggle:after {
    left: 29px;
    background-color: $whiteColor;
}

.widget.widget-themes .theme {
    border-bottom: 1px solid $navBackGround;
    display: inline-block;
    width: 100%;
    padding: 10px 0px;
}

.widget.widget-themes .theme{
    h1,h2,h3,h4,h5 {
        color: $btnPrimaryColor;
    }
}

.theme-list {
    list-style-type: none;
    margin: 0 -8px -8px 0;
    padding: 0;
}
.theme-list>li a span {
    width: 30px;
    height: 30px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    border-radius: 50%;
    display: inline-block;
    margin: 0 2px;
}

.theme-list>li.active a:after {
    content: '\f00c';
    font-family: FontAwesome;
    position: absolute;
    left: 0;
    right: -20px;
    text-align: right;
    line-height: 34px;
    top: 0;
    font-size: 18px;
    color: $whiteColor;
    color: rgba(255,255,255,0.8);
    z-index: 1020;
}

