.core-benefit-plan {
    .chose-text {
        padding: 10px 15px;
    }
    .setup-list-container {
        margin-top: 20px;
        .level-container {
            height: 50px;
            .column {
                line-height: 50px;
            }
            ul.tab {
                list-style-type: none;
                margin: 0;
                padding: 6px;
                border-radius: 5px;
                width: 100%;
                display: -webkit-flex;
                display: flex;
                line-height: 25px;
                height: 50px;
            }
            ul.tab li {
                float: left;
                padding: 0;
                flex: 1;
            }
            ul.tab li label {
                padding: 0px;
                border: 1px solid $btnSecondaryColor;
                display: block;
                margin-left: 10px;
                margin-top: -20px;
                text-align: center;
                cursor: pointer;
                border-radius: 5px;
            }
            ul.tab li input[type="radio"] {
                opacity: 0;
                width: 1px;
                height: 1px;
            }
            ul.tab li input[type="radio"]:checked~label {
                background: $color7;
                color: white;
            }
        }
    }
    .setup-footer {
        .core-plan-name {
            color: $color2;
        }
        padding: 30px;
    }
}


