body {
    // Default theme
    &.navbar-inverse {}
}

#page-container {
    margin-bottom: -80px;
}

// li.has-sub:hover .sub-menu{display: block !important;}
.page-sidebar-minified .sidebar .nav>li>a {
    padding: 12px 19px 12px 15px;
}

.page-sidebar-minified .sidebar .nav>li.sidebar-menu-item a span.icon {
    float: left;
    width: 36px;
    height: 32px;
    text-align: center;
    line-height: 32px;
    background: $blackColor;
    border-radius: 6px;
    font-size: 18px;
    margin: -7px 10px -7px 0;
    color: $sidebar-icon-color;
    position: relative;
    overflow: hidden;
}

.page-sidebar-minified .sidebar .nav>li>a>.caret {
    position: absolute;
    right: 0;
    top: 21px;
    margin-top: -4px;
    margin-right: 4px;
    border-left: 4px dashed;
    border-top: 4px solid transparent;
    border-bottom: 4px solid transparent;
}

.page-sidebar-minified .sidebar .nav>li>a>.caret.pull-right {
    margin-top: -5px;
}

.page-sidebar-minified .sidebar .nav>li.has-sub>.sub-menu {
    position: absolute;
    left: 100%;
    top: 0;
    width: 250px;
    background: $blackColor;
    padding: 10px 0 10px 45px;
}

.page-sidebar-minified .sidebar li.has-sub>.sub-menu {
    display: none !important;
}

.page-sidebar-minified .sidebar li.has-sub:hover>.sub-menu {
    display: block !important;
}


/*.page-sidebar-minified .sidebar .nav {
    position: relative;
    z-index: 10;
}*/

.nav {
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
}

.sidebar .mini-logo {
    display: none;
}

.sidebar .nav-logo {
    text-align: center;
}


