@import "partials/variables";
@import "partials/mixins";
@import "partials/layouts";
@import "partials/helper";
@import 'partials/login';
@import 'partials/admin';
@import 'partials/dashboard';
@import 'partials/settings';
@import 'partials/search_plan';
@import 'partials/bc_create_new_admin_plan';
@import 'partials/bc_update_existing_admin_plan';
@import 'partials/bc_create_marketing_plan';
@import 'partials/claims';
@import 'partials/manage_rule';
@import 'partials/bc_corporate_manage_admin_plan';
@import 'partials/_create_core_benefit_plan';
// themeing
body {
    // Default theme
    &.navbar-inverse {
        color: $color1;
        hr {
            border: 1px solid $color2 !important;
        }
        .header {
            background-color: $color4;
            .navbar-nav {
                >li {
                    >a {
                        color: $color1;
                        &:hover {
                            color: $color2;
                            background: none;
                            transition: all 1s ease;
                        }
                    }
                    .open {
                        >a {
                            color: $color2;
                            background: none;
                            transition: all 1s ease;
                            &:hover,
                            &:focus {
                                color: $color2;
                                background: none;
                                transition: all 1s ease;
                            }
                        }
                        .dropdown-menu {
                            background: $color4;
                            li {
                                a {
                                    color: $color1;
                                    &:hover,
                                    &:focus {
                                        background: $color2;
                                    }
                                }
                            }
                        }
                    }
                }
                .navbar-user {
                    .avatar {
                        color: $color1;
                        background: none;
                        font-size: 2em;
                        //border: 2px solid $color1;
                    }
                    .down-arrow {
                        color: $color1;
                    }
                }
            }
        }
        .page-container {
            .view-content {
                background: $color3;
            }
            .sidebar {
                ul.nav {
                    background: $color4;
                    >li {
                        >a {
                            color: $color1;
                            &:hover,
                            &:focus {
                                background: $color5;
                            }
                            .caret {
                                color: $color1;
                            }
                            .menu-icon {
                                background: $color3;
                                color: $color1;
                            }
                        }
                        &.nav-logo {
                            a {
                                &:hover,
                                &:focus {
                                    background: none;
                                }
                            }
                        }
                        &.divider {
                            background: $color2;
                            .sidebar-minify-btn {
                                background: $color2;
                                color: $color3;
                            }
                        }
                        &.nav-header {
                            color: $color2;
                        }
                        &.sidebar-menu-item {
                            .sub-menu {
                                background: $color6;
                                &:before {
                                    background: $color2;
                                }
                                li {
                                    a {
                                        color: $color1;
                                        &:hover,
                                        &:focus {
                                            color: $color1;
                                        }
                                        &:after {
                                            background: $color4;
                                            border: 1px solid $color2;
                                        }
                                    }
                                    &:hover,
                                    &:focus {
                                        background-color: $color7;
                                        a {
                                            &:after {
                                                background: $color2;
                                            }
                                        }
                                    }
                                }
                                .sub-menu {
                                    li {
                                        a {
                                            color: $color1;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            &.page-sidebar-minified {
                .sidebar {
                    .nav {
                        li {
                            >a {
                                color: $color1;
                                &:hover,
                                &:focus {
                                    background: $color5;
                                }
                            }
                            &.divider {
                                .sidebar-minify-btn {
                                    &:hover,
                                    &:focus {
                                        background: $color2;
                                    }
                                }
                            }
                            &.sidebar-menu-item {
                                .sub-menu {
                                    background: $color7;
                                    li {
                                        a {
                                            &:hover,
                                            &:focus {
                                                background: none;
                                                color: $color2;
                                            }
                                        }
                                    }
                                }
                                .icon {
                                    background: $color3;
                                    color: $color1;
                                }
                            }
                        }
                    }
                }
            }
        }
        .form-control {
            border: 2px solid $color1;
            color: $color1;
            background: $color3;
            &:focus,
            &:hover {
                border-color: $color2;
            }
            &[disabled] {
                //@include disabled();
                background: $color3;
                border-color: $color4;
                color: $greycolor;
            }
        }
        .btn-default {
            background: $color5;
            color: $color1;
            border: 2px solid $color5;
            .button-icon {
                border: 3px solid $color4;
            }
            &:hover,
            &:focus,
            &:active {
                background: darken($color5, 5%);
                border-color: darken($color5, 5%);
                color: $color1;
                outline: none;
            }
            &:disabled {
                background: $color7;
                border-color: $color7;
            }
        }
        .btn-secondary {
            background: $color2;
            color: $color1;
            border: 2px solid $color2;
            .button-icon {
                border: 3px solid $color4;
            }
            &:hover,
            &:focus,
            &:active {
                background: darken($color2, 5%);
                border-color: darken($color2, 5%);
                color: $color1;
                outline: none;
            }
            &:disabled {
                background: $color7;
                border-color: $color7;
            }
        }
        .input-group-btn {
            button {
                &.btn-addon {
                    border-left: 2px solid $color1;
                    &:hover {
                        background: none;
                        color: $color2;
                    }
                }
            }
        }
        .widget {
            background: $color4;
            .widget-header {
                background: $color7;
            }
            .widget-body {}
        }
        .ccsp-list {
            li {
                &:hover {
                    background: $color8;
                }
                &.selected {
                    background: $color8;
                }
            }
        }
        .ui-select-container {
            &.ui-select-bootstrap {
                .ui-select-choices-row {
                    >span {
                        color: $color1;
                        &:hover {
                            color: $color1;
                            background-color: $color8;
                        }
                    }
                    &.active {
                        >span {
                            background-color: $color9;
                            &:hover {
                                color: $color1;
                                background-color: $color8;
                            }
                        }
                    }
                }
                .ui-select-choices {
                    background: $color3;
                    div {
                        border-bottom: 1px solid $color7;
                    }
                }
                .ui-select-search {
                    width: 100% !important;
                }
                .ui-select-match {
                    .ui-select-toggle {
                        background: $color3;
                        color: $color1;
                        border: 2px solid $color1;
                        width: 100%;
                        >.caret {
                            border: 2px solid $color1;
                        }
                        &:hover,
                        &:active,
                        &:focus {
                            background: $color3;
                            color: $color1;
                            border-color: $color2;
                            .caret {
                                color: $color2;
                                border-color: $color2;
                            }
                        }
                    }
                }
            }
        }
        //UI bootstrap modal
        .modal-content {
            background: $color4 !important;
            .modal-header {
                border-bottom: 1px solid $color2;
            }
            .modal-body {
                .modal-rule-desc {
                    border: 2px solid $color7;
                    .variable-name {
                        color: red;
                    }
                    .variable-condition {
                        color: $orangeColor;
                    }
                    .variable-value {
                        color: green;
                    }
                }
            }
            .modal-footer {}
        }
        //bootstrap panel
        .panel {
            background: darken($color7, 20%);
            &.panel-default {
                >.panel-heading {
                    color: $color1;
                    background: $color6;
                    cursor: pointer;
                    +.panel-collapse {
                        >.panel-body {}
                    }
                }
            }
        }
        .plandata-table-container,
        .servicedata-table-container,
        .pharmacydata-table-container {
            .side-by-side-view {
                h4 {
                    color: $color1;
                    cursor: default;
                    background-color: $color4;
                    border: 1px solid $color4;
                    border-bottom-color: $color0;
                }
            }
            .tabs-view {}
        }
        .table {
            thead {
                tr {
                    th {
                        background: $color7;
                    }
                    &:first-child {
                        th {
                            border-top: none;
                        }
                    }
                }
            }
            tbody {
                border-top: 1px solid $color1;
                tr {
                    &:first-child {
                        td {
                            border-top: none;
                        }
                    }
                }
            }
            &.table-hover {
                thead {
                    tr {
                        th {
                            background: $color7;
                        }
                        &:first-child {
                            th {
                                border-top: none;
                            }
                        }
                    }
                }
                tbody {
                    border-top: 1px solid $color1;
                    tr {
                        &:hover {
                            td {
                                background: $color7;
                                &:first-child {
                                    border-top: none;
                                }
                            }
                        }
                        &:first-child {
                            td {
                                border-top: none;
                            }
                        }
                    }
                }
            }
        }
        .nav-tabs {
            border-bottom: none;
            li {
                &.active {
                    a {
                        color: $whiteColor;
                        cursor: default;
                        background-color: $color4;
                        border: 1px solid $color4;
                        border-bottom-color: $color0;
                        /*color: $color1;
                        cursor: default;
                        background-color: $color2;
                        border: 1px solid $color2;
                        border-bottom-color: $color0;*/
                        &:hover,
                        &:focus {
                            color: $whiteColor;
                            cursor: default;
                            background-color: $color4;
                            border: 1px solid $color4;
                            border-bottom-color: $color0;
                            /*color: $color1;
                            cursor: default;
                            background-color: $color2;
                            border: 1px solid $color2;
                            border-bottom-color: $color0;*/
                        }
                    }
                }
                a {
                    color: $greycolor;
                    cursor: pointer;
                    background-color: $disabled-bg-color;
                    border: 1px solid $disabled-bg-color;
                    border-bottom-color: $color0;
                    /*color: $color1;
                    cursor: pointer;
                    background-color: $color7;
                    border: 1px solid $color7;
                    border-bottom-color: $color0;*/
                    &:hover,
                    &:focus {
                        color: $greycolor;
                        cursor: pointer;
                        background-color: $disabled-bg-color;
                        border: 1px solid $disabled-bg-color;
                        border-bottom-color: $color0;
                        /*color: $color1;
                        cursor: pointer;
                        background-color: $color2;
                        border: 1px solid $color2;
                        border-bottom-color: $color0;*/
                    }
                }
            }
        }
    }
}

body {
    overflow-x: hidden;
    overflow-y: auto;
    font-family: "ABeeZee", sans-serif!important;
}

* {
    text-rendering: optimizeLegibility;
    -o-text-rendering: optimizeLegibility;
    -ms-text-rendering: optimizeLegibility;
    -moz-text-rendering: optimizeLegibility;
    -webkit-text-rendering: optimizeLegibility;
    -webkit-tap-highlight-color: transparent;
    box-sizing: border-box;
    outline: none;
    &:hover,
    &:focus {
        outline: none;
    }
}

.col-fifth {
    float: left;
    position: relative;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
    width: 20%;
}

.rounded-select {
    @include border-radius(50px);
    border: 2px solid $body-font-color;
    background: $color3;
}

.form-select {
    background: $color3;
    border: 0px !important;
    outline: none;
    width: 94%;
    margin: 0 0px 0 12px;
    padding: 0 !important;
    height: 30px;
    display: block;
    font-size: 14px;
    line-height: 1.42857143;
    -webkit-box-shadow: inset 0 1px 1px $form-shadow;
    box-shadow: inset 0 1px 1px $form-shadow;
    -webkit-transition: border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
    -o-transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
}

hr {
    margin: 15px 15px;
    display: inline-block;
    width: 96.5%;
}

.margin-bottom10 {
    margin-bottom: 20px;
}

.margin-right5 {
    margin-right: 5px;
}

.width100 {
    width: 100%!important;
}

.page-container {
    .header {
        z-index: 2;
        &.navbar {
            border: none;
            font-size: 14px;
            @include deep-shadow();
            z-index: 1040;
            margin-bottom: 0;
            height: $header;
            .brand {
                list-style-type: none;
                padding-left: 73px;
                margin: 0;
                .nav-logo {
                    img {
                        width: 100%;
                        max-width: 98px;
                    }
                }
                .mini-logo {
                    display: none;
                }
            }
            .nav {
                >li {
                    >a {
                        >img {
                            width: 100%;
                            max-width: 165px;
                            display: inline-block;
                            padding: 10px;
                        }
                    }
                    >div {
                        padding: 10px 5px 5px 5px;
                        >span {
                            padding: 5px;
                        }
                        &.open {
                            .dropdown-menu {
                                padding: 0;
                                top: 51px;
                                @include border-radius(0);
                                li {
                                    @include border-radius(0);
                                    a {
                                        font-size: 14px;
                                        padding: 5px 10px;
                                    }
                                    &.item-icon {
                                        .ion {
                                            width: 20px;
                                            font-size: 18px;
                                        }
                                        span {
                                            vertical-align: middle;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                &.navbar-right {
                    padding-right: 25px;
                    .avatar {
                        margin-right: 10px;
                        //font-size: 10px;
                        padding: 6px 8px 9px 8px;
                        @include border-radius(50%);
                    }
                    .navbar-user {
                        .dropdown {
                            .label-container {
                                position: relative;
                                right: 15px;
                                top: 2px;
                            }
                        }
                        .down-arrow {
                            font-size: 10px;
                            top: -2px;
                        }
                    }
                }
            }
        }
    }
    .sidebar {
        width: $sidebar;
        padding-top: $header;
        height: 100vh;
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1010;
        font-weight: 300;
        ul.nav {
            padding-left: 0;
            margin-bottom: 0;
            list-style: none;
            >li {
                >a {
                    padding: 12px 10px;
                    &:before,
                    &:after {
                        content: '';
                        display: table;
                        clear: both;
                    }
                    .caret {
                        margin-top: 10px;
                        margin-right: 2px;
                    }
                    .menu-icon {
                        float: left;
                        width: 36px;
                        height: 32px;
                        text-align: center;
                        line-height: 32px;
                        border-radius: 6px;
                        font-size: 18px;
                        margin: -7px 5px -7px 0;
                        position: relative;
                        overflow: hidden;
                    }
                }
                &.divider {
                    margin: 46px 0 10px;
                    height: 2px;
                    .sidebar-minify-btn {
                        text-align: center;
                        font-size: 8px;
                        z-index: 10;
                        padding: 1px 2px 0 0 !important;
                        width: 20px;
                        height: 20px;
                        @include border-radius(50%);
                        line-height: 15px;
                        margin: -13px auto;
                        position: relative;
                        top: -10px;
                        i {
                            font-size: 1.5em;
                            position: relative;
                            top: 3px;
                            &:before {
                                content: "\e64a";
                                font-family: 'themify';
                                speak: none;
                                font-style: normal;
                                font-weight: normal;
                                font-variant: normal;
                                text-transform: none;
                                line-height: 1;
                                -webkit-font-smoothing: antialiased;
                                -moz-osx-font-smoothing: grayscale;
                            }
                        }
                    }
                }
                &.nav-header {
                    margin-top: 25px;
                    padding: 15px 20px 10px;
                    font-size: 14px;
                    font-weight: 100;
                    letter-spacing: 2px;
                    text-transform: uppercase;
                }
                &.sidebar-menu-item {
                    .sub-menu {
                        list-style-type: none;
                        margin-left: -45px;
                        position: relative;
                        &:before {
                            content: '';
                            display: block;
                            position: absolute;
                            left: 73px;
                            top: 0;
                            bottom: 0;
                            width: 3px;
                        }
                        li {
                            width: 100%;
                            display: inline-block;
                            a {
                                padding: 10px 10px 10px 55px;
                                display: inline-block;
                                text-decoration: none;
                                position: relative;
                                &:after {
                                    content: '';
                                    position: absolute;
                                    left: 41px;
                                    top: 50%;
                                    margin-top: -6px;
                                    margin-left: -11px;
                                    width: 9px;
                                    height: 9px;
                                    border-radius: 50px;
                                }
                            }
                        }
                        .sub-menu {
                            li {
                                a {
                                    padding: 10px 20px;
                                    display: block;
                                    text-decoration: none;
                                    position: relative;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    &.page-sidebar-minified {
        .header {
            &.navbar {
                .brand {
                    padding-left: 5px;
                    .nav-logo {
                        display: none;
                    }
                    .mini-logo {
                        display: block;
                        img {
                            width: 100%;
                            max-width: 60px;
                        }
                    }
                }
            }
        }
        .sidebar {
            width: $minified-sidebar;
            position: fixed;
            ul.nav {
                position: relative;
                z-index: 10;
                >li {
                    &.divider {
                        margin: 45px 0 30px;
                        height: 2px;
                        .sidebar-minify-btn {
                            text-align: center;
                            font-size: 8px;
                            z-index: 10;
                            padding: 1px 2px 0 0 !important;
                            width: 20px;
                            height: 20px;
                            @include border-radius(50%);
                            line-height: 15px;
                            margin: -13px auto;
                            position: relative;
                            top: -10px;
                            i {
                                font-size: 1.5em;
                                position: relative;
                                top: 3px;
                                right: -1px;
                                &:before {
                                    content: "\e649";
                                }
                            }
                        }
                    }
                    &.sidebar-menu-item {
                        .menu-icon {
                            float: left;
                            width: 36px;
                            height: 32px;
                            text-align: center;
                            line-height: 32px;
                            border-radius: 6px;
                            font-size: 18px;
                            margin: -7px 10px -7px 0;
                            position: relative;
                            overflow: hidden;
                            +span {
                                display: none;
                            }
                        }
                        .sub-menu {
                            position: absolute;
                            width: 300px;
                            top: 0px;
                            right: -300px;
                            padding: 0;
                            &:before {
                                left: 22px;
                            }
                            li {
                                a {
                                    padding: 10px 10px 10px 40px;
                                    width: 100%;
                                    &:after {
                                        left: 30px;
                                    }
                                }
                            }
                        }
                    }
                    &.has-sub {
                        &:hover,
                        &:hover {}
                    }
                }
            }
        }
        .view-content {
            margin-left: $minified-sidebar;
        }
    }
    &.page-sidebar-fixed {
        .sidebar {
            position: fixed;
            bottom: 0;
        }
    }
    .view-content {
        min-height: 100vh;
        top: 50px;
        z-index: 0;
        margin-left: $sidebar;
        padding: 25px 10px;
        position: relative;
        .content-header {
            font-size: 30px;
            margin-bottom: 20px;
            span {
                font-size: 26px;
            }
        }
    }
}

.form-control {
    @include border-radius(50px);
    &:focus {
        outline: 0;
        box-shadow: none;
    }
}

.form-horizontal {
    .form-group {
        height: 100%;
        min-height: 50px;
        .control-label {
            padding-top: 7px;
            margin-bottom: 0;
            text-align: right;
            padding-right: 0;
        }
    }
}

.btn {
    @include border-radius(50px);
    outline: none;
    &.btn-default {
        padding: 0;
        height: 35px;
        span:not(.button-icon) {
            position: relative;
            top: -3px;
            left: 6px;
            padding-right: 10px;
            padding-left: 10px;
        }
        .button-icon {
            @include border-radius(50%);
            padding: 2px 10px;
            position: relative;
            right: -3px;
            font-size: 23px;
            &.ion-ios-more {
                padding: 2px 8px;
            }
            &.ion-ios-search {
                padding: 4px 9px;
            }
            &.ion-md-more {
                padding: 2px 13px;
            }
            &.ion-ios-search-outline {
                padding: 2px 4px;
            }
            &.ion-ios-close-circle {
                padding: 2px 6px;
            }
            &.ion-md-brush {
                padding: 2px 5px;
            }
            &.ion-ios-bookmark {
                padding: 2px 8px;
            }
            &.ion-md-checkmark {
                padding: 2px 7px;
            }
            &.ion-ios-eye {
                padding: 4px 9px;
            }
            &.ion-edit {
                padding: 6px 9px;
                font-size: 18px;
            }
            &.ion-ios-arrow-up-outline {
                padding: 4px 10px;
                font-size: 22px;
            }
            &.ion-ios-person {
                padding: 4px 10px;
                font-size: 22px;
            }
            &.ion-ios-color-filter {
                padding: 4px 8px;
                font-size: 22px;
            }
        }
        &.no-icon {
            padding: 5px 12px;
        }
    }
    &.btn-secondary {
        padding: 0;
        height: 35px;
        span:not(.button-icon) {
            position: relative;
            top: -3px;
            right: 11px;
            padding-left: 10px;
            padding-right: 10px;
        }
        .button-icon {
            @include border-radius(50%);
            padding: 2px 10px;
            position: relative;
            left: -3px;
            font-size: 23px;
            &.ion-ios-eye {
                padding: 2px 7px;
            }
            &.ion-ios-close {
                padding: 4px 8px;
            }
        }
        .no-icon {
            padding: 5px 12px;
        }
    }
    &:hover,
    &:focus {
        outline: none;
    }
    .btn-block {
        width: 100%;
    }
}

.md-block {
    .btn {
        width: 100%;
        &.btn-default {
            .button-icon {
                right: -9px;
                font-size: 23px;
