.rule-container {
    position: absolute;
    width: 98%;
    z-index: 100;
    background: $subFormBackgroundColor;
    box-shadow: 0px 0px 2px 1px;
    select{
        min-width: 100px;
    }
    label{
        padding-left: 0px;
        padding-right: 0px;
        text-align: left;
        min-width: 50px;
    }
    .form-select {
        width: 85%;
    }
    .header-section{
        width:100%;
        height: 50px;
        font-size: 19px;
        border-bottom: 1px solid;
        line-height: 45px;
    }
    .manage-rule-container{
        border-right: 1px solid;
        min-height: 550px;
        padding-bottom: 20px;
    }
    .margin-top-10{
        margin-top: 10px;
    }
    .margin-left-20{
        margin-left: 20px;
    }
    .dynamic-rule-container{
        width: 100%;
        min-height: 115px;
        box-shadow: 0px 0px 1px 2px;
        line-height: 25px;
        margin-top: 20px;
        margin-bottom: 20px;
        padding-bottom: 10px;
        .single-rule{
            margin-top: 5px;
            margin-bottom: 5px;  
        }
        .delete-icon{
            float: right;
            margin-top: 5px;
            .icon{
                font-size: 24px;
                color: $whiteColor;
                cursor: pointer;
            }
        }
    }
    .rule-footer{
        text-align: right;
    }
    .rule-list-container{
        .list-header{
            border-bottom: 1px solid;
            div{
                line-height: 30px;
            }
            icon{
                font-size: 20px;
            }
        }
        .list-body-container{
            .single-list{
                height:50px;
                border: 1px solid;
                margin: 10px;
                padding-left: 5px;
                padding-top: 2px;
            }
        }
    }
}


