body{
    height: 100%;
    background: linear-gradient( $login-bg-color, $login-bg-color ), url('/assets/images/healthcare.jpg') no-repeat center center fixed;
    background-size: cover;
}
body{
    // Default theme
    &.navbar-inverse{
        .login-container{
            .login-form-container{  background: $color4;  }
        }
    }
}

.content-container{
    .title{
        font-size: 18px;
        text-align: center;
        font-weight: bold;
        padding-top:150px;
        padding-bottom: 40px;
    }
    .login-box {
        max-width: 1000px;
        .login-img{
            width:100%;
        }
    }

    .login-container{
        .login-form-container{
            padding: 25px;
            .signin-button{
                margin:30px 0 0px;
            }
        }
    }
}



