.search-plan-container,
.qulifier {
    .sub-container{
        border-top: 2px solid $color2;
        padding: 10px;
    }
    h4{
        padding-bottom: 10px;
    }
    label {
        padding-right: 5px;
        text-align: right;
        min-width: 170px;
        font-weight: normal;
    }
    .form-group {
        height: 35px;
    }    
    .form-heading {
        margin-bottom: 10px;
    }
    
    .plan-search {
        height: 34px;
        .form-select {
            height: 30px;
        }
    }
    .ui-select-container {
        width: auto !important;        
        .btn.btn-default {
            height: 24px;
            margin-left: 5px;
        }
        .close {
            color: $whiteColor;
            opacity: .5;
        }
        .close:hover,
        .close:focus {
            color: $whiteColor;
            opacity: 1;
        }
        .ui-select-match-item:hover,
        .ui-select-match-item:focus {
            .close {
                opacity: 1;
            }
        }
    }
}

body.navbar-inverse input.form-control.border-highlight.ng-dirty.ng-not-empty {
    border: 2px solid $btnPrimaryColor;
}

.search-results-container {
    div {
        padding: 10px 0px;
    }
}

.view-plan {
    .title-container {
        padding: 10px 0px;
        font-weight: bold;
        font-size: 16px;
    }
}

.qulifier {
    label {
        min-width: 200px;
    }
    input {
        max-width: 150px;
    }
}


