.page-sidebar-minified .content {
    margin-left: 70px;
}

.page-sidebar-minified .content {
    margin-left: 70px;
}

.bc_dashboard_class,
.bc_admin_dashboard_class {
    .widget-header {
        background: $disabled-color;
        display: inline-block;
        width: 100%;
        height: 50px;
    }
    canvas {
        background: $color4;
    }
    .chart {
        padding-top: 15px;
    }
    .bc_dashboard p {
        display: inline;
        padding: 8px;
    }
    .col-sm-2 {
        padding-left: 5px;
        padding-right: 5px;
    }
    .col-sm-8 {
        padding-left: 0px;
        padding-right: 5px;
    }
    .col-sm-3,
    .col-sm-4 {
        padding-left: 2px;
        padding-right: 5px;
    }
    .notification_div {
        display: inline-block;
        padding-left: 7px;
    }
    table {
        width: 100%;
        height: 100px;
    }
    h4 {
        padding: 15px;
        font-size: 16px;
        margin: 0;
        font-weight: bold;
    }
    h3 {
        padding-left: 15px;
    }
    p {
        padding: 7px;
    }
    i{
        font-size: 15px;
        color: $orangeColor;
    }
    th {
        text-align: center;
        padding: 7px;
        font-size: 12px;
        font-weight: normal;
    }
    td {
        text-align: center;
        font-size: 18px;
        font-weight: 800;
    }
    .widget-body,
    .bc_notification_div {
        background: $color4;
        border-radius: 5px;
        font-size: 15px;
    }
    .bc_dashboard_table {
        background: $color4;
        border-radius: 5px;
    }
    .graph_container_2 {
        position: relative;
        top: 15px;
    }
    .notification_div {
        font-size: 12px;
    }
    .col-sm-6,
    .col-sm-12 {
        padding-left: 5px;
        padding-right: 5px;
    }
    .graph_tab {
        position: relative;
        top: 15px;
    }
    img {
        width: 100%;
        height: 100%;
    }
}

.bc_admin_dashboard_class {
    th {
        text-align: center;
        padding: 15px;
        font-size: 12px;
        font-weight: normal;
    }
}

.pie-chart-container {
    width: 220px;
    height: 220px;
    margin: 0 auto;
}

.line-chart-container {
    width: 100%;
    height: 220px;
    margin: 0 auto;
}


