.entry-form-container {
    .ng-dirty {
        border-color: $color1 !important;
    }
    label {
        min-width: 120px;
    }
    select {
        min-width: 100px;
    }
    input[text] {
        min-width: 170px;
    }
    .allign-name {
        margin-top: -20px;
    }
    .plan-id {
        margin-top: 10px;
    }
    .plan-id-val {
        margin-left: -20px;
    }
    .select-admin-plan {
        width: 51%;
        display: inline-block;
        .form-select {
            width: 82%;
        }
    }
    .select-claim-plan {
        width: 65%;
        display: inline-block;
        max-width: 315px;
        .form-select {
            width: 82%;
        }
    }
    .padding-none {
        padding-left: 0px;
        padding-right: 0px;
    }
    .border-none {
        border: none !important;
    }
    .disabled {
        background: $disabled-bg-color;
        color: $disabled-color;
        cursor: not-allowed;
        border-color: $disabled-color;
        span {
            color: $disabled-color !important;
        }
    }
    .add-claim {
        margin-right: 2%;
        margin-top: 20px;
    }
    .amount-selection {
        border: 2px solid;
        border-radius: 20px;
        width: 136px;
        span {
            display: inline-block;
            margin-top: 4px;
            background: transparent;
            float: left;
            border: none;
            color: $whiteColor;
        }
        input.form-control {
            border: 0px;
            width: 100px;
            display: inline-block;
            padding-left: 0px;
        }
    }
    .add-amount-container {
        label {
            min-width: 130px;
        }
    }
    .form-heading {
        margin-bottom: 10px;
    }
    .ui-select-container {
        input {
            width: 100% !important;
        }
        .ui-select-match {
            width: 100%;
            span {
                width: 100%;
            }
        }
    }
    .biil-amount {
        margin-top: 15px;
    }
    .button-icon {
        font-size: 20px;
        cursor: pointer;
    }
}

.adjudication-form-container {
    .title-container {
        background: $table-header-color;
        text-align: center;
        height: 40px;
        padding-top: 1px;
    }
    .claims-adjudication-table {
        margin: 20px 0;
        height: 50vh;
        overflow-y: auto;
    }
    .side-by-side-view {
        div {
            overflow-x: auto;
            margin-bottom: 10px;
        }
        table {
            margin-bottom: 10px;
            th,
            td {
                min-width: 150px;
            }
        }
    }
}

.provider-form-container {
    .plan_details {
        padding-top: 15px;
    }
    .provider-header_data {
        padding-top: 15px;
    }
}


